
.pinkBg {
  background: #FF71C0 !important;
}
.fa-icon {
  width: auto;
  height: 1em;
  /* or any other relative font sizes */
}
.list-post {
  padding: 10px;
  border-radius: 6px;
  background: #fff;
  margin-bottom: 20px;
}
.list-post .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  cursor: pointer;
}
.list-post .title .name {
  font-size: 18px;
  color: #FF71C0;
}
.list-post .title .date {
  font-size: 12px;
  color: #333;
}
.list-post .options {
  height: 50px;
}



