
.pinkBg {
  background: #FF71C0 !important;
}
.post {
  background: #fff;
  padding: 20px;
}
.post-title {
  line-height: 1;
  text-align: center;
}
.post-date {
  font-size: 12px;
  font-weight: 400;
  line-height: 2;
  color: #999;
  text-align: right;
}
.post-tags {
  margin-top: 10px;
  text-align: center;
  color: #666 ;
  font-size: 12px;
}
.post-tags i {
  display: inline-block;
  padding: 2px 5px;
  border: 1px solid #666;
  border-radius: 10px;
  margin-right: 2px;
}
.post article pre {
  background: #f7f7f7 !important;
  padding: 16px;
  overflow-x: auto;
  white-space: pre;
}
.post article code {
  background: #f7f7f7;
}
.post article blockquote {
  margin: 10px 0;
  padding: 0 16px;
  border-left: 4px solid #ddd;
}
.post article h3,
.post article h4,
.post article h5 {
  padding: 10px 0;
}
.post article p {
  line-height: 20px;
  padding: 10px 0;
}
.post article p img {
  width: 100%;
}
.post article a {
  display: inline-block;
  color: #FF71C0;
  margin: 5px 0;
}
.post article ol,
.post article ul {
  padding-left: 20px;
}



