
.friend {
  background: #fff;
  border-radius: 8px;
  padding: 20px;
}
.friend-wrap {
  margin-bottom: 10px;
  border-bottom: 1px solid #f0f0f0;
}
.friend-wrap a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  text-decoration: none;
  color: #333;
}
.friend-wrap a .avatar img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.friend-wrap a .info {
  padding-left: 10px;
}
.friend-wrap a .info .name {
  font-size: 14px;
}
.friend-wrap a .info .des {
  padding: 5px 0;
  font-size: 12px;
}



