
.pinkBg {
  background: #FF71C0 !important;
}
.mailme {
  margin-top: 20px;
  background: #fff;
  border-radius: 8px;
  padding: 20px;
}
.mailme-header p {
  font-size: 20px;
  color: #FF71C0;
}
.mailme-header p img {
  width: 20px;
  height: 20px;
}
.mailme-form {
  margin-top: 10px;
}
.mailme-form-wrap input,
.mailme-form-wrap textarea {
  width: 180px;
  background: #FF71C0;
  border: none;
  font-size: 14px;
  border-radius: 4px;
  color: #fff;
  line-height: 18px;
  height: 40px;
  padding: 0 5px;
  outline: none;
  margin-bottom: 15px;
}
.mailme-form-wrap input::-webkit-input-placeholder,
.mailme-form-wrap textarea::-webkit-input-placeholder {
  color: #fff;
  font-size: 14px;
}
.mailme-form-wrap textarea {
  width: 90%;
  padding-top: 5px;
  height: 80px;
}
.mailme-form-wrap a {
  cursor: pointer;
  border-radius: 5px;
  text-decoration: none;
  padding: 5px 10px;
  font-size: 18px;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
  -o-transition: .3s;
  display: inline-block;
  color: #FF71C0;
  border: 2px #FF71C0 solid;
}
.mailme-form-wrap a:hover {
  background-color: #FF71C0;
  color: #fff;
}



